const TxInfo: React.FC<{
    t: Transaction;
}> = ({ t }) => (
    <>
        <div>
            {Object.keys(t).map((k, i) => (
                <IfExists t={t} k={k} key={i} />
            ))}
        </div>
    </>
);

interface IfExistsProps {
    t: Transaction;
    k: string;
}

const IfExists: React.FC<IfExistsProps> = ({ t, k, type }) => {
    if (!t[k]) {
        return null;
    }

    switch (typeo) {
        case "string":
            return (
                <div>
                    {k}: {t[k]}
                </div>
            );
    }
};

